const formatCreateTime = time => {
  const startTime = new Date(time)
  const endTime = new Date()
  const timeDiff = Math.abs(endTime - startTime)
  const timeAgo = Math.floor(timeDiff / 1000 / 60)

  if (timeAgo >= 60 * 24 * 365) {
    return ' '
  }
  if (timeAgo >= 60 * 24 * 30) {
    return `${Math.floor(timeAgo / 30 / 24 / 60)} `
  }
  if (timeAgo >= 60 * 24) {
    return `${Math.floor(timeAgo / 24 / 60)} `
  }
  if (timeAgo >= 60) {
    return `${Math.floor(timeAgo / 60)} `
  }
  if (timeAgo > 0) {
    return `${timeAgo} `
  }
  return '1 '
}
export default formatCreateTime
