import { useNavigate } from 'react-router-dom'

import s from './NotFoundPage.module.scss'

import { LargeButton, LargeWhiteButton } from 'components/Common/Button/Large/LargeButton'
import img from 'assets/img/icon-404.png'

const NotFoundPage = () => {
  const navigate = useNavigate()
  const clickMainHome = () => {
    navigate('/')
  }
  const clickBackPage = () => {
    navigate(-1)
  }
  return (
    <div className={s.container}>
      <img className={s.img} src={img} alt='   ' />
      <section className={s.text}>
        <h1 className={s.header}>   </h1>
        <p className={s.check}>
                .
          <br />
            
        </p>
        <div className={s.mainButton}>
          <LargeButton onClickEvent={clickMainHome}></LargeButton>
          <LargeWhiteButton onClickEvent={clickBackPage}></LargeWhiteButton>
        </div>
      </section>
    </div>
  )
}

export default NotFoundPage
