import { useState } from 'react'

import s from './Footer.module.scss'

import gitHub from 'assets/img/icon-github.svg'
import Modal from '../Modal/Modal'

const Footer = ({ list = false }) => {
  const [isModalOpen, setIsModalOpen] = useState(false)

  const closeModal = () => setIsModalOpen(false)

  return (
    <>
      <footer className={list ? s.list : s.footer}>
        <div className={s.text}>
          <p>      </p>
          <p>2023 , , , . All rights reserved.</p>
        </div>
        <button className={s.gitHub} onClick={() => setIsModalOpen(true)} type='button'>
          <img src={gitHub} alt='' />
        </button>
      </footer>
      {isModalOpen && (
        <Modal closeModal={closeModal}>
          <div className={s.container}>
            <p className={s.title}>GitHub</p>
            <ul className={s.listBtn}>
              <li>
                <a href='https://github.com/FRONTENDSCHOOL5/final-16-GreenTangerineMarket'>  </a>
              </li>
              <li>
                <a href='https://github.com/ho-ji'>  </a>
              </li>
              <li>
                <a href='https://github.com/Limttugi'>  </a>
              </li>
              <li>
                <a href='https://github.com/tmfdk0213'>  </a>
              </li>
              <li>
                <a href='https://github.com/Seunggyu-Jung'>  </a>
              </li>
            </ul>
          </div>
        </Modal>
      )}
    </>
  )
}

export default Footer
