import s from './Splash.module.scss'

import logo from 'assets/img/logo.svg'
import charLogo from 'assets/img/logo_char.svg'

const Splash = () => {
  return (
    <dialog className={s.container} open>
      <div className={s.logoWrapper}>
        <h1 className='a11y-hidden'> </h1>
        <img className={s.logo} src={logo} alt='  ' />
        <img className={s.charLogo} src={charLogo} alt='  ' />
      </div>
      <p className={s.explain}>    </p>
    </dialog>
  )
}

export default Splash
